@import './../../utils/variables';

.departure-port {
  display: flex;
  background-color: $color-1;
  color: white;

  .departure-row-inner-left {
    width: 100%;
    height: 100%;
    padding: 1em;
    align-self: center;
    text-align: right;
  }

  .departure-row-inner-right {
    width: 100%;
    height: 100%;
    padding: 1em;
    align-self: center;
    text-align: left;
    color: $color-4;
    font-size: x-large;
  }

  .departure-active {
    text-decoration: underline;
    text-underline-position: under;
  }
}

.departure-port__loading {
  width: 100%;
  height: 1em;
  padding: 1em;
}
